import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Slider from "@mui/material/Slider";

const dispatchedEvents = [];

const SrsMuiSlider = function (props, ref) {
  return <Slider {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiSlider component */
`;

const props = [
  {
    name: "value",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "defaultValue",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "min",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "max",
    defaultValue: 100,
    type: "number",
  },
  {
    name: "step",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "marks",
    defaultValue: null,
    type: "object",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Slider if needed
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSlider),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
